.slider {
  padding-top: 40px;
  margin-bottom: 115px;

  .product__image {
    // height: 284px;
    aspect-ratio: 0.95 / 1;

    // @media (max-width: $desktop) {
    //   // height: 340px;
    // }

    // @media (max-width: 600px) {
    //   height: 136px;
    // }
  }

  @media (max-width: $desktop) {
    margin-bottom: 67px;
  }

  @media (max-width: $tablet) {
    margin-bottom: 55px;
  }
}

.slider-container {
  position: relative;

  @media (max-width: $desktop) {
    padding: 0 45px;
  }

  @media (max-width: 767px) {
    padding: 0 15px;
  }
}

.slider__heading {
  @media (max-width: $desktop) {
    margin-bottom: 20px;
  }
}

.slider__heading-link {
  @media (max-width: $desktop) {
    margin-bottom: 55px;
  }

  @media (max-width: $tablet) {
    margin-bottom: 44px;
  }
}
